import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanPlan } from './bean/interest-plan.bean';
import { ConfirmationService } from 'primeng/primeng'
// import { IsRcord, IsAppointed, IsFazhi, IsB } from './../constant/codeValue';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";


@Component({
  selector: 'app-interest-plan',
  templateUrl: './interest-plan.component.html',
  styleUrls: ['./interest-plan.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class InterestPlan implements OnInit {
  date3: any;
  beanPlan: BeanPlan = new BeanPlan();
  ch: any = CALENDAR_CN;
  first: number = 0;//分页控制
  //table数据
  list: any = [];
  list1: any = [];
  list2: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  isRcode: any = [];
  isAppointed: any = [];
  isFazhi: any = [];
  isB: any = [];
  code: any;//获取所有码值
  total: number;
  effDt: Date;
  invalDt: Date;
  invalDtStart: Date;
  invalDtEnd: Date;
  tabMesg: any;//表格中无数据时显示的内容
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc) {
  this.codeValues();
    this.isFazhi = this.code['IsFazhi'];
    this.isAppointed = this.code['IsAppointed'];
    this.isB = this.code['IsB'];
    this.isRcode = this.code['IsRcord'];
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }

    this.beanPlan.pageSize = 10;
    this.beanPlan.pageNum = 1;
    this.httpService.queryRateTypeInternalListService(this.beanPlan).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy")
        this.list1.unshift({ label: '请选择', value: '' })
      }
    });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.beanPlan.pageSize = 10;
    this.beanPlan.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  //查询
  toquery() {
    this.beanPlan.effDt = this.commfunc.transDateN(this.effDt);//生效日期
    this.beanPlan.invalDt = this.commfunc.transDateN(this.invalDt);//失效日期
    this.beanPlan.invalDtStart = this.commfunc.transDateN(this.invalDtStart);
    this.beanPlan.invalDtEnd = this.commfunc.transDateN(this.invalDtEnd);

    this.httpService.queryRatePlanInternalService(this.beanPlan).subscribe(data => {
      // 暂无数据提示
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }

      // 数据
      this.list = data.resultList
      this.total = data.total;

      // 错误条件查询的提示
      if (data.returnCode.code != "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.tabMesg = tableMessage
    });

  }
  //重置
  newSet() {
    this.beanPlan = new BeanPlan();
    this.effDt = null;
    this.invalDt = null;
    this.invalDtStart = null;
    this.invalDtEnd = null;
    this.beanPlan.pageSize = 10;
    this.beanPlan.pageNum = 1;
    // this.first =0;
    // this.toquery();
  }
  //添加
  add(param) {
    this.router.navigate(['/pages/tzb/valuation/interest-plan/interest-plan-add'])
  }
  //新增的回调函数
  addCall(param) {
    this.display = true;
  }
  //详情
  detail(param) {
    this.headerTitle = '利率计划-基本信息';
    this.showModel = '3';
    this.display = true;
    this.inValueCode = param;
  }

  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //更新
  update(param) {
    this.headerTitle = '利率计划-修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param;
  }
  //修改的回掉函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    } this.toquery()
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.beanPlan.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.beanPlan.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanPlan.pageSize;
    //调用查询的方法
    this.toquery();
  }
  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          intRatePlnNum: car.intRatePlnNum,
          ccyCode: car.ccyCode,
          intRatePlnMtrxSerlNum: car.intRatePlnMtrxSerlNum,
          effDt: car.effDt,
        };
        this.httpService.deleteRatePlanService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.toquery();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
          // if (data.errorMessage == null) {
          // } else {
          //   this.msgs = [];
          //   this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          // }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        })
      }
    });
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
