import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
// import { IsDeadlineAdd, IsType, IsMoney, IsDeadline, IsGrade, IsB, IsUnit, IsContact, IsFazhi, IsAppointed } from './../../constant/codeValue';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { decimalInteger, specialSymbol } from 'app/@uisftech/common/service/validator';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
