import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanPlan } from './../bean/interest-plan.bean';
import { Message } from 'primeng/primeng';
// import { IsB, IsUnit, IsFazhi, IsAppointed } from './../../constant/codeValue';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';
import { decimalNum, TwoInteger, decimalInteger, decimalInt } from 'app/@uisftech/common/service/validator';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
@Component({
  selector: 'app-add-details',
  templateUrl: './add-details.component.html',
  styleUrls: ['./add-details.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class AddDetailsComponent implements OnInit {
  inVal: string = 'T0000';
  beanPlan: BeanPlan = new BeanPlan();
  list: any = [];
  list1: any = [];
  list3: any = [];
  list4: any = [];
  list5: any = [];
  list7: any = [];
  car: any;
  decimalInt = decimalInt;
  decimalInteger = decimalInteger;
  isFazhi: any = [];
  isAppointed: any = [];
  isB: any = [];
  isUnit: any = [];
  code: any;//获取所有码值
  //传入的值
  @Input() inValue: any;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // //传入详情的值
  // inValueCode: string;
  //2选择树
  showModel: string;
  institArea2: any;
  //模态框
  display: boolean = false;
  //提示信息
  msgs: Message[] = [];
  //弹出框的头信息
  headerTitle: string;
  treeOrgName: any;
  orgName: string;
  orgId: string;
  userform: FormGroup; //校验 
  isDisabledAfldIntRateMat: boolean = false;
  isDisabledAfldIntNumRatio: boolean = false;
  isDisabledFlotTpCode: boolean = false;
  isDisabledIntRateVal: boolean = false;
  isDisabledIntRateUnitTpCode: boolean = false;
  isDisabledHghstIntRateLmt: boolean = false;
  isDisabledMnIntRateLmt: boolean = false;
  isIntMainRateCgy: boolean = false;
  isDisabledIntFlotVal: boolean = false;//利率浮动类型
  orgAreaName;
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public fb: FormBuilder,
    public commfunc: Commfunc,
    public commonHttpService: CommonHttpService,
  ) { 
    this.codeValues();
    this.isFazhi = this.code['IsFazhi'];
    this.isAppointed = this.code['IsAppointed'];
    this.isB = this.code['IsB'];
    this.isUnit = this.code['IsUnit'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'planPrtyPlnLvl': new FormControl('', Validators.required),
      'spcDmsnlCode': new FormControl(''),
      'numRatio': new FormControl(''),
      'institArea': new FormControl(''),
      'reservedField1': new FormControl(''),
      'reservedField2': new FormControl(''),
      'intRateAsgnWayCode': new FormControl('', Validators.required),
      'afldIntNumRatio': new FormControl(''),
      'intRateCgy': new FormControl(''),
      'afldIntRateMat': new FormControl(''),
      'flotTpCode': new FormControl(''),
      'intFlotVal': new FormControl('', [Validators.nullValidator, decimalNum]),
      'intRateVal': new FormControl('', [Validators.nullValidator, TwoInteger]),
      'intRateUnitTpCode': new FormControl(''),
      'intRateLmtTpCode': new FormControl(''),
      'hghstIntRateLmt': new FormControl(''),
      'mnIntRateLmt': new FormControl(''),
    });
    this.httpService.queryRateTypeInternalListService(this.beanPlan).subscribe(data => {
      if (data.resultList) {
        this.list1 = this.commfunc.toChangeTree(data.resultList, "intRateName", "intRateCgy")
        this.list1.unshift({ label: '请选择', value: '' });
      }
    });
    let param1 = { dmsnlChiNm: "特殊积数比" };
    this.httpService.querydetailTmsDicService(param1).subscribe(data => {
      if (data.resultList) {
        this.list3 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list3.unshift({ label: '请选择', value: '' })
      }
    });
    // let param2 = { dmsnlChiNm: "积数比" };
    // this.httpService.querydetailTmsDicService(param2).subscribe(data => {
    //   if (data.resultList) {
    //     this.list4 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
    //     this.list4.unshift({ label: '请选择', value: '' })
    //   }
    // });
    // let param3 = { dmsnlChiNm: "期限" };
    // this.httpService.querydetailTmsDicService(param3).subscribe(data => {
    //   if (data.resultList) {
    //     this.list5 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
    //   }
    // });
    let param5 = { dmsnlChiNm: "特定维度值" };
    this.httpService.querydetailTmsDicService(param5).subscribe(data => {
      if (data.resultList) {
        this.list7 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue");
        this.list7.unshift({ label: '请选择', value: '' })
      }
    });
    this.isDisabledHghstIntRateLmt = true;
    this.isDisabledMnIntRateLmt = true;
  }
   //码值
   codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //利率指定方式、利率浮动类型
  intRateAsgnWayCodeChange() {
    switch (this.beanPlan.intRateAsgnWayCode) {
      case '':
        this.isDisabledAfldIntRateMat = false;
        this.isDisabledAfldIntNumRatio = false;
        this.isDisabledFlotTpCode = false;
        this.isDisabledIntFlotVal = false;
        this.isDisabledIntRateVal = false;
        this.isDisabledIntRateUnitTpCode = false;
        this.isIntMainRateCgy = true;
        this.beanPlan.intRateCgy = ''
        break;
      case '1':
        this.isDisabledAfldIntRateMat = true;
        this.beanPlan.afldIntRateMat = '';
        this.isDisabledAfldIntNumRatio = true;
        this.beanPlan.afldIntNumRatio = '';
        this.isDisabledFlotTpCode = true;
        this.beanPlan.flotTpCode = '';
        this.isDisabledIntFlotVal = true;
        this.beanPlan.intFlotVal = '';
        this.isDisabledIntRateVal = false;
        this.isDisabledIntRateUnitTpCode = false;
        this.isIntMainRateCgy = true;
        this.beanPlan.intRateCgy = ''
        break;
      case '2':
        // case '3':
        this.isDisabledAfldIntRateMat = false;
        this.isDisabledAfldIntNumRatio = false;
        this.isDisabledFlotTpCode = false;
        this.isDisabledIntFlotVal = false;
        this.isDisabledIntRateVal = true;
        this.beanPlan.intRateVal = '';
        this.isDisabledIntRateUnitTpCode = true;
        this.beanPlan.intRateUnitTpCode = '';
        this.isIntMainRateCgy = true;
        if (this.inValue) {
          this.beanPlan.intRateCgy = this.inValue;
        }

        let param2 = { dmsnlChiNm: "积数比", intRateCgy: this.inValue };
        this.httpService.queryLoansBaseRateListService(param2).subscribe(data => {
          if (data.resultList) {
            this.list4 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
          }
        });
        let param3 = { dmsnlChiNm: "期限", intRateCgy: this.inValue };
        this.httpService.queryLoansBaseRateListService(param3).subscribe(data => {
          if (data.resultList) {
            this.list5 = this.commfunc.toChangeTree(data.resultList, "dmslValDesc", "dmsnlValue")
          }
        });
        if (this.beanPlan.flotTpCode == '1') {
          this.isDisabledIntFlotVal = true;
          this.beanPlan.intFlotVal = '';
        } else if (this.beanPlan.flotTpCode == '2') {
          this.isDisabledIntFlotVal = false;
        } else if (this.beanPlan.flotTpCode == '3') {
          this.isDisabledIntFlotVal = false;
        }
        break;
      default:
        break;
    }

  }
  //利率阀值类型
  intRateLmtTpCodeChange() {
    // let item = this.list[i];
    this.beanPlan.hghstIntRateLmt = "";
    this.beanPlan.mnIntRateLmt = "";

    if (!this.beanPlan.intRateLmtTpCode || this.beanPlan.intRateLmtTpCode == "9") {
      this.isDisabledHghstIntRateLmt = true;
      this.isDisabledMnIntRateLmt = true;
    } else {
      this.isDisabledHghstIntRateLmt = false;
      this.isDisabledMnIntRateLmt = false;
    }
  }
  //校验上限阈值
  checkInterest() {
    if (this.beanPlan.hghstIntRateLmt == '') {
      return false;
    }

    if (this.beanPlan.intRateLmtTpCode == '1' || this.beanPlan.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.beanPlan.hghstIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }
  checkRate() {
    if (this.beanPlan.hghstIntRateLmt == '') {
      return false;
    }

    if (this.beanPlan.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.beanPlan.hghstIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  //校验下限阈值
  checkInter() {
    if (this.beanPlan.mnIntRateLmt == '') {
      return false;
    }

    if (this.beanPlan.intRateLmtTpCode == '1' || this.beanPlan.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.beanPlan.mnIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  checkRa() {
    if (this.beanPlan.mnIntRateLmt == '') {
      return false;
    }

    if (this.beanPlan.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.beanPlan.mnIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }
  //公共机构树点击事件
  chooseTree(param) {
    this.headerTitle = '选择机构树';
    this.showModel = '2';
    this.display = true;
    // this.inVal = param;
    // console.log(this.inVal)
  }

  //公共机构树回掉
  treeCall(param) {
    this.display = false;
    this.beanPlan.institArea = param.orgId;
    this.orgAreaName = param.orgName;
    // let params = {
    //   orgId: param.orgId,
    //   relationshipType: "0"
    // }
    // this.commonHttpService.queryOrgByOrgIdAndRelationship(params).subscribe(data => {
    //   console.log(data.orgList)
    //   this.treeOrgName = this.commfunc.toChangeTree(data.orgList, "orgName", "orgId")
    //   console.log(this.treeOrgName)
    //   this.beanPlan.institArea = this.treeOrgName[0].label;
    //   param.orgId = this.treeOrgName[0].value;
    //   console.log(param.orgId)
    //   this.institArea2 = this.beanPlan.institArea;
    // })
  }
  //清空机构树
  clean() {
    this.beanPlan.institArea = "";
    this.orgAreaName="";
  }
  //提交
  save() {
    //利率指定方式为1固定值时，利率值、利率单位类型非空判断
    if (this.beanPlan.intRateAsgnWayCode == '1' && (!this.beanPlan.intRateVal || !this.beanPlan.intRateUnitTpCode)) {
      return;
    }
    //利率指定方式为2挂靠，利率浮动类型非空判断
    if (this.beanPlan.intRateAsgnWayCode == '2' && !this.beanPlan.flotTpCode) {
      return;
    }
    //
    if ((this.beanPlan.flotTpCode == '2' || this.beanPlan.flotTpCode == '3') && !this.beanPlan.intFlotVal) {
      return;
    }
    //利率阀值类型不为空，最高 最低阀值非空判断
    if (this.beanPlan.intRateLmtTpCode && this.beanPlan.intRateLmtTpCode != '9') {
      if (!this.beanPlan.mnIntRateLmt || !this.beanPlan.hghstIntRateLmt)
        return;
    }

    //上限校验
    if (this.beanPlan.intRateLmtTpCode == '1' || this.beanPlan.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.beanPlan.hghstIntRateLmt)) {
        return;
      }
    }
    if (this.beanPlan.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.beanPlan.hghstIntRateLmt)) {
        return;
      }
    }
    //下限校验
    if (this.beanPlan.intRateLmtTpCode == '1' || this.beanPlan.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.beanPlan.mnIntRateLmt)) {
        return;
      }
    }
    if (this.beanPlan.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.beanPlan.mnIntRateLmt)) {
        return;
      }
    }
    // console.log(this.beanPlan['planPrtyPlnLvl'])
    //校验
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    };

    if (!this.orgAreaName) {
      this.orgAreaName = '';
    }
    let addMap = {
      planPrtyPlnLvl: this.beanPlan['planPrtyPlnLvl'], spcDmsnlCode: this.beanPlan.spcDmsnlCode,
      numRatio: this.beanPlan.numRatio, institArea: this.orgAreaName, institArea2: this.beanPlan.institArea, reservedField1: this.beanPlan.reservedField1,
      reservedField2: this.beanPlan.reservedField2, intRateAsgnWayCode: this.beanPlan.intRateAsgnWayCode,
      intRateCgy: this.beanPlan.intRateCgy, afldIntRateMat: this.beanPlan.afldIntRateMat,
      afldIntNumRatio: this.beanPlan.afldIntNumRatio, flotTpCode: this.beanPlan.flotTpCode,
      intFlotVal: this.beanPlan.intFlotVal, intRateVal: this.beanPlan.intRateVal, intRateUnitTpCode: this.beanPlan.intRateUnitTpCode,
      intRateLmtTpCode: this.beanPlan.intRateLmtTpCode, hghstIntRateLmt: this.beanPlan.hghstIntRateLmt, mnIntRateLmt: this.beanPlan.mnIntRateLmt,
    }
    console.log(addMap)
    this.outValue.emit(addMap)
  }
  //返回
  back() {
    this.outValue.emit(false)
  }
  //利率浮动值
  // intFlotValValidate() {
  //   let value = this.beanPlan.intFlotVal;
  //   let reg = /^[+|-]{0,1}[0-9]{0,7}(\.[0-9]{0,7})?$/;
  //   if (!reg.test(value)) {
  //     this.beanPlan.intFlotVal = '';
  //     this.msgs = [];
  //     this.msgs.push({ severity: 'error', summary: '提示', detail: '利率浮动值只能为7位整数7位小数！' });
  //   }
  // }

}
